/**
 * [Tags Group] at the beginning of each single post
 */
.tags-group-top {
  margin: 0;
  list-style: none;

  .tag {
    display: inline-block;

    > a {
      display: block;
      margin-right: 0.4rem;
      margin-bottom: 0.6rem;
      font-size: 1rem;
      text-decoration: underline;
      color: $blue;

      &:hover,
      &:active {
        color: darken($blue, 10%);
        transition: all 0.2s ease-in;
      }
    }
  }
}

/**
 * [Tags Group] at the bottom of each single post
 */
.tags-group-bottom {
  margin: 0;
  list-style: none;

  .tag {
    display: inline-block;

    > a {
      display: block;
      margin-right: 0.4rem;
      margin-bottom: 0.6rem;
      padding: 0.2rem 0.4rem;
      border-radius: 5px;
      font-size: 1.1rem;
      text-decoration: none;
      background-color: $green;
      color: $white;

      &:hover,
      &:active {
        background-color: darken($green, 10%);
        transition: all 0.2s ease-in;
      }
    }
  }
}